/*!
 * Main style of Wish List for WooCommerce plugin  	 
 *
 * @version 1.3.2
 * @since   1.0.0
 * @author  Algoritmika Ltd.
 */
.alg-wc-wl-view-state {
  display: none;
}

.alg-wc-wl-btn {
  position: relative;
  outline: none;
  cursor: pointer;
}

.alg-wc-wl-btn:focus {
  outline: none;
}

.alg-wc-wl-btn:hover i.fa {
  transform: translateZ(0) scale(1.5, 1.5);
}

.alg-wc-wl-btn i.fa {
  transition: all 0.14s ease-in-out;
  margin-left: 9px;
  backface-visibility: hidden;
}

.alg-wc-wl-btn .fa.loading {
  position: absolute;
  right: -30px;
  top: 50%;
  margin: -9px 0 0 0;
  font-size: 18px;
  opacity: 0;
  display: none;
  color: #333;
}

.alg-wc-wl-btn.loading .fa.loading {
  opacity: 0.5;
  display: block;
}

.alg-wc-wl-btn.add .alg-wc-wl-view-state-add {
  display: block;
}

.alg-wc-wl-btn.add i.fa {
  opacity: 0.7;
}

.alg-wc-wl-btn.remove .alg-wc-wl-view-state-remove {
  display: block;
}

.alg-wc-wl-btn.remove i.fa {
  opacity: 1;
}

.alg-wc-wl-btn.remove i.fa:first-child {
  color: #DC3232;
}

.alg-wc-wl-toggle-btn {
  display: inline-block !important;
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 8px 12px;
  float: none !important;
}

.alg-wc-wl-remove-btn .alg-wc-wl-remove-item-from-wl {
  border: none;
}

.alg-wc-wl-remove-item-from-wl {
  margin: 0;
}

.woocommerce.single-product .alg-wc-wl-thumb-btn, .woocommerce .products .alg-wc-wl-thumb-btn {
  padding: 0;
  top: 13px;
  right: 13px;
  position: absolute;
  font-size: 0;
  z-index: 2;
  display: none;
}

.woocommerce.single-product .alg-wc-wl-thumb-btn div i, .woocommerce .products .alg-wc-wl-thumb-btn div i {
  font-size: 20px;
  margin: 0;
}

.woocommerce .products .alg-wc-wl-toggle-btn {
  margin: 0 auto 10px auto;
}

/* Wishlist view table  */
.alg-wc-wl-view-table {
  width: 100%;
}

.alg-wc-wl-view-table .product-thumbnail img {
  max-width: 70px;
  margin: 0 auto;
  height: auto;
}

@media (max-width: 768px) {
  .alg-wc-wl-view-table .product-thumbnail img {
    display: inline-block;
  }
}

.alg-wc-wl-view-table td, .alg-wc-wl-view-table tr {
  vertical-align: middle !important;
  text-align: center !important;
}

.alg-wc-wl-view-table th {
  text-align: center;
}

.alg-wc-wl-view-table td {
  padding: 15px;
}

@media (max-width: 768px) {
  .alg-wc-wl-view-table td {
    text-align: right !important;
  }
}

@media (max-width: 768px) {
  .alg-wc-wl-view-table.shop_table_responsive thead {
    display: none;
  }
  .alg-wc-wl-view-table.shop_table_responsive tbody tr:first-child td:first-child {
    border-top: 0;
  }
  .alg-wc-wl-view-table.shop_table_responsive tbody th {
    display: none;
  }
  .alg-wc-wl-view-table.shop_table_responsive tr {
    display: block;
  }
  .alg-wc-wl-view-table.shop_table_responsive tr td {
    display: block;
    text-align: right !important;
  }
  .alg-wc-wl-view-table.shop_table_responsive tr td.order-actions {
    text-align: left !important;
  }
  .alg-wc-wl-view-table.shop_table_responsive tr td:before {
    content: attr(data-title);
    font-weight: 700;
    float: left;
  }
  .alg-wc-wl-view-table.shop_table_responsive tr td.product-remove:before, .alg-wc-wl-view-table.shop_table_responsive tr td.actions:before {
    display: none;
  }
  .alg-wc-wl-view-table.shop_table_responsive tr:nth-child(2n) td {
    background-color: rgba(0, 0, 0, 0.025);
  }
}

.alg-wc-wl-empty-wishlist {
  display: none;
}

/* Notifications */
.alg-wc-wl-notification-link {
  display: block;
  margin-top: 5px;
  color: #fff;
  text-decoration: underline;
}

.alg-wc-wl-notification-link:hover {
  color: #fff;
}

.alg-wc-wl-social {
  margin-bottom: 17px;
}

.alg-wc-wl-share-on {
  vertical-align: middle;
  margin-right: 5px;
}

.alg-wc-wl-social-ul {
  display: inline-block;
  font-size: 0px;
  vertical-align: middle;
  margin: 0;
  padding: 0;
}

.alg-wc-wl-social-li {
  vertical-align: middle;
  list-style: none;
  font-size: 30px;
  display: inline-block;
  margin-right: 5px;
}

.alg-wc-wl-social-li a {
  opacity: 0.6;
  transition: all 0.2s ease-in-out;
}

.alg-wc-wl-social-li a:focus {
  outline: none;
}

.alg-wc-wl-social-li a:hover, .alg-wc-wl-social-li a.active {
  opacity: 1;
}

.alg-wc-wl-email-options {
  display: none;
  margin: 10px 0 20px 0;
  padding: 20px;
  background: #FBFBFB;
}

.alg-wc-wl-email-options form {
  margin: 0;
}

.alg-wc-wl-email-options .label-description {
  color: #999;
  font-weight: normal;
  margin-left: 10px;
  font-size: 13px;
  position: relative;
  top: -1px;
}

.alg-wc-wl-email-options .alg-wc-wl-email-admin {
  margin-top: 9px;
}

.alg-wc-wl-email-options .alg-wc-wl-email-admin input {
  margin-right: 5px;
}

.alg-wc-wl-email-options .alg-wc-wl-email-admin label {
  margin: 0;
}

.alg-wc-wl-email-options .alg-wc-wl-email-admin * {
  vertical-align: middle;
}

.alg-wc-wl-email-options label {
  margin-bottom: 10px;
  display: inline-block;
  cursor: pointer;
}

.alg-wc-wl-email-options .alg-wc-wl-emails-input {
  width: 100%;
}

.alg-wc-wl-email-options input[type="submit"] {
  margin-top: 15px;
  clear: right;
  float: right;
  padding: 10px;
  text-transform: uppercase;
  font-weight: bold;
}

.alg-wc-wl-email-options textarea {
  max-width: 100%;
  min-width: 100%;
  margin-top: 10px;
  min-height: 90px;
}

.alg-wc-wl-email-options textarea, .alg-wc-wl-email-options .alg-wc-wl-emails-input {
  padding: 6px 10px;
}

/* IZITOAST (for notifications) */
.iziToast > .iziToast-body > .iziToast-buttons {
  display: block !important;
  margin: 0 -2px 5px -2px !important;
}

.iziToast > .iziToast-body > .iziToast-buttons > *:focus {
  outline: none;
}

.iziToast .iziToast-body {
  padding-left: 55px !important;
}

.iziToast .slideIn {
  margin-right: 0 !important;
}

/* Woocommerce Product page Thumbnail ============================= */
.single-product div.product .images {
  position: relative;
}

/* Woocommerce Loop Product page Thumbnail ============================= */
.woocommerce-LoopProduct-link {
  position: relative;
}

*[class*="alg-"] i.fa {
  font-family: FontAwesome !important;
  height: auto;
}
